import Vue from 'vue'
import VueRouter from 'vue-router'
import MYCV from '../views/MyCV.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'MyCV',
    component: MYCV
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
